import React, { FC } from 'react';
import OrderDetailsStyle from '../order-details/order-details.module.css'
import SuccessfulOrder from '../../images/done.png';
import { useSelector } from '../../services/hooks';

const OrderDetails: FC = () => {
    const orderNumber = useSelector((store) => store.ingredients.order);
    const orderNumberRequest = useSelector((store) => store.ingredients.sendOrderRequest);
    const orderNumberSuccess = useSelector((store) => store.ingredients.sendOrderSuccess);

    if (orderNumberRequest) {
        return (
            <span className={`${OrderDetailsStyle.text} text text_type_main-medium`}>...</span>
        )
    } else if (orderNumberSuccess) {
        return (
            <>
                <h1 className={`${OrderDetailsStyle.title} text text_type_digits-large  mt-5 mb-8`}>{orderNumber}</h1>
                <span className={`${OrderDetailsStyle.text} text text_type_main-medium`}> </span>
                <img className={`${OrderDetailsStyle.image} mt-10 mb-10`} src={SuccessfulOrder} alt="  " />
                <span className={`${OrderDetailsStyle.text} text text_type_main-default`}>   </span>
                <span className={`${OrderDetailsStyle.text} mt-8 text text_type_main-default text_color_inactive`}>    </span>
            </>
        )
    } else {
        return (
            <>
                <h1 className={`${OrderDetailsStyle.title} text text_type_digits-large  mt-5 mb-8`}>!</h1>
                <span className={`${OrderDetailsStyle.text} text text_type_main-medium`}>  </span>
            </>
        )
    }



}


export default OrderDetails